.container_header {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2; }

.container_name {
  height: 60px;
  background-color: #099d8d;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; }

.container_nav {
  height: 45px;
  background-color: #14d9c4;
  font-size: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%; }

.item {
  font-family: "Rubik Medium";
  color: white;
